.skeleton-loader {
  width: 100%;
  height: max-content;
  background: linear-gradient(to right,
      #ffffff00,
      #ffffff80,
      #ffffff00),
    #d3d3d3;
  background-repeat: repeat-y;
  background-size: 90px 50px;
  animation: loading 3s infinite;
}

@keyframes loading {
  to {
    background-position: 110%
  }
}